/* General Layout */
.container {
  max-width: 100%;
  padding: 10px;
  font-family: Arial, sans-serif;
  color: #333;
}

.title {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 15px;
  color: #007bff; /* Blue color for title */
}

.form-section, .game-list-section {
  margin: 15px 0;
}

.form-section {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  background-color: #f9f9f9;
}

.game-list-section {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  background-color: #f9f9f9;
}

/* Button Styles */
button {
  padding: 10px 16px;
  margin: 5px;
  border: none;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.3s, color 0.3s;
  background-color: #007bff;
  color: white;
  font-size: 0.9rem;
}

/* Button Styles Specific to Visibility Handling */
.scroll-to-game-list, .go-up {
  position: fixed;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  z-index: 1000;
  transition: opacity 0.3s, visibility 0.3s;
}

/* Hide buttons when not needed */
.hidden {
  opacity: 0;
  visibility: hidden;
}

/* Specific Styles for Buttons */
.scroll-to-game-list {
  top: 20px;
  right: 20px;
  background-color: #007bff;
}

.go-up {
  bottom: 20px;
  right: 20px;
  background-color: #28a745;
}

.scroll-to-game-list:hover, .go-up:hover {
  opacity: 0.8;
}

/* Media Queries for Mobile Devices */
@media (max-width: 767px) {
  .title {
    font-size: 1.8rem;
  }

  .form-section, .game-list-section {
    padding: 10px;
  }

  button {
    padding: 8px 12px;
    font-size: 0.8rem;
  }

  input {
    padding: 8px;
    font-size: 0.8rem;
  }
}
